/* import styled from '@emotion/styled'; */


/*
 * Стили компонента Searchbar
 */
.Searchbar {
    top: 0;
    left: 0;
    position: sticky;
    z-index: 1100;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    color: #fff;
    background-color: #ffedd5;

    border-radius: 5px;

    /* box-shadow: 0px 2px 4px -1px 
                rgba(0, 0, 0, 0.2),
                0px 4px 5px 0px 
                rgba(0, 0, 0, 0.14), 
                0px 1px 10px 0px 
                rgba(0, 0, 0, 0.12); */

    box-shadow: 7px 7px 3px 0px rgba(0, 0, 0, 0.50);
    
}


/*
 * Стили компонента SearchForm
 */
.SearchForm {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
}


/*
 * Стили SearchFormButton
 */
.SearchFormButton {
    display: inline-block;
    width: 90px;
    height: 50px;
    border: 0;
    /* background-image: url('https: //www.flaticon.com/ru/free-icon/statistics_254220?related_id=254220&origin=pack'); */
    /* background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');  */
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.SearchFormButton:hover {
    opacity: 1;
}

.SearchFormButtonLabel {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
}


/*
 * Стили SearchFormInput
 */
.SearchFormInput {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 24px;
    border: none;
    outline: none;
    padding-left: 32px;
    padding-right: 4px;
}

.SearchFormInput::placeholder {
    font: inherit;
    font-size: 18px;
}